html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background: #F5F5F5;
  font-family: 'Roboto', sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

#root {
  max-width: 959px;
  margin: 0 auto;
  width: 100%;
  -ms-flex: 1 1;
      flex: 1 1;
}

#disclaimer {
  display: block;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  opacity: 0.7;
}

@media only screen and (max-width: 959px) {
  #disclaimer {
    display: none !important;
  }
}



